.expertise-main-container {
  width: 100%;
  height: 150vh;
  background-color: #161616;
  background-image: url('../images/work-bckgrnd.png');
  background-size: cover;
}

.expertise-title {
  color: white;
  font-size: 30px;
  text-transform: uppercase;
  position: absolute;
  top: 17%;
  left: 50%;
  transform: translate(-50%);
  letter-spacing: 3px;
}

.expertise-main-content {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 80%;
}

.expertise-content {
  height: calc(100vw / 6);
  width: calc(100vw / 6);
  margin: -1px;
  background-color: rgba(0, 0, 0, 0.7);
  color: #bd9a35;
  font-size: 27px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #bd9a35;
  border-bottom: 1px solid #bd9a35;
  cursor: pointer;
}